<template>
  <div class="dialogBox">
    <div class="mainTable_search">
      <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        :tableOperate="tableOperate"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
  </div>
</template>
<script>
export default {
  name: "achiCheck",
  data() {
    return {
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,
      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "订单编号",
          placeholder: "请输入订单编号"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "交易日期",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "daterange"
        }
      ],
      tableHead: [
        { name: "礼品卡号", value: "name" },
        { name: "可用余额 (元)", value: "date" },
        { name: "赠送余额 (元)", value: "address" },
        { name: "有效期", value: "progress" },
        { name: "状态", value: "id" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "王小明",
          status: "1",
          address: "上海市普陀区金沙江路 1518 弄",
          progress: 10
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "大宝",
          status: "2",
          address: "上海市普陀区金沙江路 1517 弄",
          progress: 30
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "咔咔西",
          status: "1",
          address: "上海市普陀区金沙江路 1519 弄",
          progress: 60
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "王小虎",
          status: "1",
          address: "上海市普陀区金沙江路 1516 弄",
          progress: 90
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "王小虎",
          status: "2",
          address: "上海市普陀区金沙江路 1516 弄",
          progress: 100
        }
      ]
    };
  },
  // 注册组件
  components: {
  },
  props: {
    formData: {
      type: Object,
      default: {
        store: "",
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: "",
        time: ""
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    // 初始化
    init() {
      console.log(this.formData);
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>




