<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>{{tempName}}</div>
    </div>
    <div class="mainTable_inputBox">
      <el-form
        :model="formData"
        :rules="rules"
        size="medium"
        ref="formData"
        label-width="120px"
        label-position="right"
      >
        <el-form-item label="交易金额" prop="name">
          <el-input v-model="formData.name" placeholder="请选输入交易金额" clearable></el-input>
        </el-form-item>
        <el-form-item label="POS订单编号" prop="name">
          <el-input v-model="formData.name" placeholder="请选输入POS订单编号" clearable></el-input>
        </el-form-item>
        <!-- <div class="posTxt fontRed">注：请务必保证线下POS订单编号的准确性以免发生消费失败的情况.</div> -->
        <el-form-item label="交易动态码" prop="name">
          <el-input v-model="formData.name" placeholder="请选输入交易动态码" clearable></el-input>
        </el-form-item>
        <div class="posTxt" style="color:#606266;">交易动态码请使用扫码枪对准会员储值支付二维码进行扫码扣款操作.</div>
      </el-form>
      <div slot="footer" class="dialog-footer ac">
        <el-button type="primary" @click="submitForm('formData')">确 认</el-button>
        <el-button class="ml20" @click="cancelForm">取 消</el-button>
      </div>
    </div>
    <el-tabs tab-position="left" @tab-click="tabClick" value="store" stretch>
      <el-tab-pane name="store" label="礼品卡">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
      <el-tab-pane name="clerk" label="电子钱包">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "writeList",
  data() {
    return {
      tempName: "核销明细",
      // 验证
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ]
      },
      type: "",
      searchData: {},

      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "订单编号",
          placeholder: "请输入订单编号"
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "手机号码",
          placeholder: "请输入手机号码"
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "交易日期",
          bplaceholder: "开始时间",
          eplaceholder: "结束时间",
          timeType: "daterange"
        }
      ],

      tableBut: [{ name: "导 出", value: "export", show: true }],
      tableHead: [
        { name: "会员昵称", value: "name" },
        { name: "订单编号", value: "date" },
        { name: "POS订单编号", value: "address" },
        { name: "手机号码", value: "progress" },
        { name: "礼品卡金额", value: "id" },
        { name: "赠送金额", value: "id" },
        { name: "订单金额", value: "id" },
        { name: "优惠金额", value: "id" },
        { name: "折扣率", value: "id" },
        {
          name: "交易状态",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "男" : "女");
          },
          filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
        },
        { name: "交易时间", value: "id" }
      ],
      tableData: []
    };
  },
  // 注册组件
  components: {
  },
  methods: {
    tabClick(e) {
      this.type = e.name;
      console.log(e.name);
      if (e.name == "clerk") {
        this.tableHead = [
          { name: "订单编号", value: "" },
          { name: "门店编码", value: "" },
          { name: "门店名称", value: "" },
          { name: "店员名称", value: "" },
          { name: "手机号码", value: "" },
          { name: "消费金额", value: "" },
          { name: "交易时间", value: "" }
        ];
      } else {
        this.tableHead = [
          { name: "会员昵称", value: "" },
          { name: "订单编号", value: "" },
          { name: "POS订单编号", value: "" },
          { name: "手机号码", value: "" },
          { name: "礼品卡金额", value: "" },
          { name: "赠送金额", value: "" },
          { name: "订单金额", value: "" },
          { name: "优惠金额", value: "" },
          { name: "折扣率", value: "" },
          {
            name: "交易状态",
            value: "status",
            format: (row, column, cellValue, index) => {
              return (cellValue = cellValue == 1 ? "男" : "女");
            },
            filter: [{ text: "男", value: "1" }, { text: "女", value: "2" }]
          },
          { name: "交易时间", value: "id" }
        ];
      }
    },

    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      this.$exportData(this.tableHead, this.tableData, this.tempName);
    },
    check(e) {
      console.log(e);

      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(`每页 ${e} 条`);
    },
    currentChangeEvent(e) {
      console.log(`当前页: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.mainTable_inputBox {
  width: 560px;
  padding-bottom: 40px;
}
.el-tabs {
  padding-top: 30px;
  border-top: 1px solid rgba(192, 204, 218, 0.5);
}
.posTxt {
  padding: 0 0 30px 120px;
}
</style>



