import login from '@/views/login';
import index from '@/views/index';
import home from '@/views/home';
import NotFound from '@/views/404';
// 核销管理
import writeList from '@/views/writeOff/writeList';
// 门店
import Clerk from '@/views/store/Clerk';
import Achievement from '@/views/store/Achievement';
import Account from '@/views/store/Account';
// 财务-信息





let routes = [{
    path: '/login',
    name: 'login',
    component: login,
    hidden: true
  }, {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  //  {
  //   path: '/home',
  //   component: index,
  //   // redirect: { name: '用户信息' },
  //   name: 'home',
  //   iconCls: 'icon-tab_index',
  //   meta: {
  //     title: '首页'
  //   },
  // },
  {
    path: '/writeOff',
    component: index,
    name: 'writeOff',
    iconCls: 'icon-POS_accounting',
    meta: {
      title: '核销管理'
    },
    children: [{
      path: '/writeOff/writeList',
      component: writeList,
      name: 'writeList',
      meta: {
        title: '核销明细'
      }
    }, ]
  },{
    path: '/store',
    component: index,
    name: 'store',
    iconCls: 'icon-POS_shop',
    meta: {
      title: '店务管理'
    },
    children: [{
      path: '/store/Clerk',
      component: Clerk,
      name: 'Clerk',
      meta: {
        title: '店员管理'
      },
    }, {
      path: '/store/Achievement',
      component: Achievement,
      name: 'Achievement',
      meta: {
        title: '售卡业绩'
      },
    },{
      path: '/store/Account',
      component: Account,
      name: 'Account',
      meta: {
        title: '账户明细'
      },
    },]
  }
];

export default routes;



